@import "./common.css";

* {
  color: var(--color-black);
}

h1 {
  font-weight: 700;
  font-size: 1.5rem;
  letter-spacing: 0;
  margin: 0 auto 3rem;
}

#wrapper,
form {
  align-items: center;
}

#wrapper {
  position: absolute;
  overflow-x: hidden;
  overflow-y: scroll;
  width: 100vw;
  min-height: 100vh;
  align-content: center;
  justify-content: center;
  display: inline-grid;
  padding: 3.5rem 0;
  background-color: var(--color-bg);
  top: 2vh;
  transition: top opacity;
  transition-duration: 0.25s;
  transition-delay: 0.15s;
  transition-timing-function: ease-in-out;
  opacity: 0;
}

#wrapper.hydrated {
  top: 0;
  opacity: 1;
}

form {
  width: 100%;
  max-width: 340px;
  padding: 48px 32px;
  display: flex;
  flex-direction: column;
  background-color: var(--color-form);
}

form > div {
  margin-bottom: 32px;
}
form > div:last-child {
  margin-bottom: 0;
}

.number-input-container {
  width: 256px;
  margin: 0 1rem;
  display: grid;
  grid-template-columns: 48px auto 48px;
}

label#number_of_players,
label#number_of_spies {
  display: inline-block;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-align: center;
  width: 100%;
  color: var(--color-blue-dark);
}

.number-input,
input[type="number"] {
  width: 100%;
  background-color: var(--color-mustard-extra-light);
}

.number-input {
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

input[type="number"] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  appearance: none;
  position: relative;
  min-width: 100%;
  height: 48px;
  border: 1px solid var(--color-mustard-light);
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  border-radius: none;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  transition: all 0.2s ease-out;
  -moz-appearance: textfield;
}

input[type="number"]:focus {
  background-color: var(--color-white);
  border: 1px solid var(--color-mustard-midtone);
  outline: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

button.button-decrement,
button.button-increment {
  position: relative;
  height: 100%;
  padding: 12px 16px;
  background-color: var(--color-mustard-extra-light);
  border: 1px solid var(--color-mustard-light);
  border-radius: none;
  transition: all 0.1s ease-out;
  cursor: pointer;
  -webkit-appearance: none;
  -webkit-transform: scale(1);
  transform: scale(1);
}

button {
  margin: 0;
  color: var(--color-black);
}

button:active,
button:focus {
  outline: 0;
}

button.button-decrement::after,
button.button-increment::after {
  content: "";
  position: absolute;
  opacity: 1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transition: inherit;
  background-position: center;
  background-repeat: no-repeat;
}

button:disabled {
  pointer-events: none;
}

button:disabled::after {
  opacity: 0.25;
}

button[type="submit"] {
  padding: 1rem;
  font-weight: 700;
  font-size: 1.125rem;
  width: 256px;
  margin: 0 auto;
  border: 0;
  background-color: var(--color-black);
  color: var(--color-white);
  transition-duration: 80ms;
  text-transform: uppercase;
}

button[type="submit"]:hover {
  background-color: var(--color-black-hover);
}

button[type="submit"]:active {
  background-color: var(--color-blue-dark);
  transform: scale(1.025);
}

.button-decrement::after {
  background-image: var(--minus-dark);
}

.button-increment::after {
  background-image: var(--plus-dark);
}

.button-decrement {
  border-right: none;
}

.button-increment {
  border-left: none;
}

@media (hover: hover) {
  button:hover,
  input[type="number"]:hover {
    background-color: var(--color-white);
  }
  button:active {
    background-color: var(--color-mustard-extra-light);
  }
}

@media (hover: none) {
  button:active {
    background-color: var(--color-white);
  }
}

#de_flag,
#en_flag,
#es_flag,
#fr_flag,
#it_flag {
  height: 38px;
  width: 38px;
  border-radius: 19px;
  background-color: #e2e2e2;
}

label {
  display: inline-block;
  margin-bottom: 0;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 0 0 var(--color-black);
  box-shadow: 0 0 0 0 var(--color-black);
  transition-duration: 80ms;
}

#lang_selector input:checked + label {
  transition-duration: 80ms;
  -webkit-box-shadow: 0 0 0 3px var(--color-black);
  box-shadow: 0 0 0 3px var(--color-black);
}

#en_flag {
  background: url(../assets/flags/gb.svg);
}

#it_flag {
  background: url(../assets/flags/it.svg);
}

#de_flag {
  background: url(../assets/flags/de.svg);
}

#es_flag {
  background: url(../assets/flags/es.svg);
}

#fr_flag {
  background: url(../assets/flags/fr.svg);
}

#es_flag,
#fr_flag,
#it_flag,
#de_flag,
#en_flag,
#options fieldset label::before {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

details,
fieldset#lang_selector {
  width: 340px;
  background-color: var(--color-form);
}

fieldset#lang_selector {
  display: flex;
  margin: 0 auto;
  padding: 1rem 2rem 2rem;
  border: 0;
  justify-content: space-between;
}

details {
  margin-top: 2rem;
}

summary {
  font-size: 1rem;
  font-weight: 700;
  padding: 2rem;
  text-align: center;
}

#options fieldset {
  display: flex;
  flex-direction: column;
  width: auto;
  gap: 0.65rem;
  border: 0;
  padding: 0 2rem 2rem;
}

#options fieldset label {
  color: var(--color-black);
  font-size: 1.1rem;
  padding: 0.35rem 0.35rem 0.35rem 2rem;
  width: 100%;
  position: relative;
  cursor: pointer;
}

#options fieldset label::before {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  content: "";
  width: 1rem;
  height: 1rem;
  border: solid 2px #000;
  background-image: url(../assets/check-mark.svg);
  transition-duration: 80ms;
}

#options fieldset input:checked + label::before {
  background-color: var(--color-black);
  border: solid 2px var(--color-black);
}

#options fieldset input[type="checkbox"] {
  display: none;
}

#options fieldset legend {
  text-transform: uppercase;
  color: var(--color-blue-dark);
  width: 100%;
  text-align: center;
  padding-bottom: 1rem;
}
